.promos-image {
	margin: 0 14px 20px;
	@include radius(5px);
	overflow: hidden;
	box-shadow: 0 5px 9px rgba($color: $black, $alpha: 0.1);
	display: flex;
	align-items: center;
	justify-content: center;
	> img {
		width: 100%;
	}
}

.cards-professionals {
	padding: 20px 16px;
	&__header {
		display: flex;
		align-items: center;
		margin-bottom: 28px;
	}
	&__avatar {
		min-width: 60px;
		margin-right: 15px;
		position: relative;
		.avatar {
			@include circle-img(60px, 60px);
		}
		&.checked {
			&::after {
				content: "";
				position: absolute;
				bottom: -7.8px;
				left: 50%;
				margin-left: -7.8px;
				width: 13.6px;
				height: 13.6px;
				background: url(../img/icons/checked.svg) no-repeat center center / 100% 100%;
			}
		}
	}
	&__info {
		width: 80%;
		a {
			color: $text-color;
			font-size: $base-fs + 2px;
			line-height: $base-line-height + 2px;
			letter-spacing: -0.41px;
			margin-bottom: 7px;
		}
		span {
			font-size: $base-fs - 4px;
			line-height: $base-line-height - 2px;
			color: $text-gray;
			display: inline-block;
			letter-spacing: -0.07px;
		}
	}
	&__desc {
		font-size: $base-fs - 2px;
		line-height: $base-line-height + 4px;
		letter-spacing: -0.41px;
		> li {
			border-top: 0.5px solid $border-light-gray;
			padding: 11.5px 0;
			span {
				color: $text-gray;
				font-size: $base-fs - 3px;
				line-height: $base-line-height - 4px;
			}
			.border-violet {
				margin-top: 7px;
			}
			p {
				color: $text-gray;
				font-size: $base-fs - 4px;
				line-height: $base-line-height - 4px;
				> span {
					margin-left: 10px;
					display: inline-block;
					height: 12.8px;
					vertical-align: middle;
				}
			}
		}
	}
	.btn {
		width: 290px;
		margin: 11px auto 0;
		display: flex;
	}
}

@media only screen and (max-width: 490px) {
	.cards-professionals {
		.btn {
			width: 100%;
		}
	}
}

@media only screen and (max-width: 860px) and (orientation: landscape) {
	.cards-professionals {
		&__info {
			width: 90%;
		}
	}
}
