.notifications__card { padding: 14px 17px 30px; font-size: 12px; line-height: 16px; }

.notifications__arrow { font-size: 11px; line-height: 18px; letter-spacing: -0.08px; color: #979797; margin-bottom: 13px; }

.notifications__arrow > img { cursor: pointer; }

.notifications__arrow > img.rotate { transform: rotate(180deg); -webkit-transform: rotate(180deg); -moz-transform: rotate(180deg); -ms-transform: rotate(180deg); -o-transform: rotate(180deg); }

.notifications__header { line-height: 18px; }

.notifications__header span { display: block; font-size: 11px; color: #979797; }

.notifications__header .avatar { flex: 0 0 30px; display: flex; align-items: center; justify-content: center; width: 30px; height: 30px; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; overflow: hidden; margin-right: 10px; }

.notifications__body { display: none; padding-left: 40px; margin-top: 11px; }

.notifications__body > a { margin-bottom: 20px; }

.notifications__body p { margin-bottom: 20px; }

.notifications__body p:last-of-type { margin-bottom: 0; }

.user { width: 100%; background: url(../img/background-image.png) no-repeat top center; background-size: 375px; min-height: 100vh; padding-top: 1px; }

.user__header { position: relative; width: 122px; margin: 40px auto 27px; }

.user__header .edit-icon { position: absolute; bottom: 0; left: -2px; }

.user__avatar { position: relative; display: flex; align-items: center; justify-content: center; width: 122px; height: 122px; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; overflow: hidden; }

.user__avatar > img { width: 100%; }

.user__avatar .edit-photo { position: absolute; top: 0; right: 0; bottom: 0; left: 0; background: linear-gradient(180deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 462.85%); display: none; align-items: center; justify-content: center; }

.user__checked { display: flex; position: absolute; bottom: -15px; left: 50%; margin-left: -15px; }

.user__name { font-weight: 500; font-size: 17px; line-height: 22px; margin-bottom: 2px; text-align: center; }

.user .job-title { display: flex; align-items: center; font-size: 12px; text-align: center; margin-bottom: 20px; }

.user .job-title > span { display: inline-block; padding: 0 4px 0 2px; }

.user__info { display: flex; justify-content: space-between; flex-wrap: wrap; max-width: 300px; margin: 0 auto 28px; font-size: 12px; }

.user__info > p { display: flex; align-items: center; }

.user__info > p > img { margin-right: 7px; }

.user__card { position: relative; padding: 10px 13px 20px; font-size: 13px; line-height: 24px; }

.user__card .edit-info { position: absolute; top: 10px; right: 13px; z-index: 10; display: none; }

.user__card_title { margin-bottom: 20px; font-size: 15px; line-height: 20px; color: #000; }

.user__card_info { display: flex; padding-bottom: 24px; position: relative; }

.user__card_icon { display: flex; align-items: center; justify-content: center; width: 40px; height: 40px; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; overflow: hidden; background-color: #dadae4; margin-right: 11px; min-width: 40px; }

.user__card_text { width: 100%; }

.user__card_group-btn { position: absolute; left: 50px; right: 0; bottom: 0; z-index: 10; display: flex; justify-content: space-between; }

.user__card_group-btn .btn { height: 17px; width: auto; font-size: 10px; }

.user__card_group-btn.edit-group-btn { display: none; }

.user__validated { position: absolute; bottom: 0; right: 0; display: flex; align-items: center; color: #7e20c8; font-size: 10px; font-weight: 500; height: 18px; }

.user__validated > img { margin-left: 6px; }

.user .btn-save { position: fixed; bottom: 70px; left: 50%; transform: translateX(-50%); -webkit-transform: translateX(-50%); -moz-transform: translateX(-50%); -ms-transform: translateX(-50%); -o-transform: translateX(-50%); display: none; z-index: 100; }

.user__meetup-btn { padding: 20px 0; border-top: 1px solid #d5d5e0; }

.user .btn-meetup-connected { position: relative; }

.user .btn-meetup-connected::after { content: url(../img/icons/checked-circle.svg); position: absolute; top: 3px; right: 3px; }

.user.edit-profile { padding-bottom: 45px; }

.user.edit-profile .edit-info { display: block; }

.user.edit-profile .edit-photo { display: flex; }

.user.edit-profile .btn-save { display: flex; }

.user.edit-profile .edit-group-btn { display: flex; }

.style-input-text { font-size: inherit; font-weight: 400; height: 21px; border: none; background: #fff; color: #000; width: 100%; box-shadow: none; border-radius: 0; -webkit-border-radius: 0; -moz-border-radius: 0; -ms-border-radius: 0; -o-border-radius: 0; }

.style-input-text_right { text-align: right; }

.style-input-text_15 { font-size: 15px; }

.style-input-text_12 { font-size: 12px; }

.style-input-text::placeholder { color: #979797; }

.style-input-text:disabled { color: #000; -webkit-text-fill-color: #000; opacity: 1; }

.style-textarea { font-size: inherit; font-weight: 400; line-height: 24px; width: 100%; background: #fff; color: #000; resize: none; }

.style-textarea::placeholder { color: #979797; }

.style-textarea:disabled { color: #000; -webkit-text-fill-color: #000; opacity: 1; }

.dashboard .title { margin: 31px 0 40px; }

.dashboard__card { padding: 14px 16px 20px; min-height: 105px; }

.dashboard__header { position: relative; }

.dashboard__header .arrow-down { position: absolute; top: 0; right: 0; transition: transform 0.4s; -webkit-transition: transform 0.4s; -moz-transition: transform 0.4s; -ms-transition: transform 0.4s; -o-transition: transform 0.4s; }

.dashboard__header .arrow-down.open { transform: rotate(180deg); -webkit-transform: rotate(180deg); -moz-transform: rotate(180deg); -ms-transform: rotate(180deg); -o-transform: rotate(180deg); }

.dashboard__date { margin-bottom: 13px; font-size: 11px; color: #979797; }

.dashboard__info { display: flex; align-items: center; margin-top: 13px; }

.dashboard__info > p { font-weight: 400; margin-left: auto; line-height: 24px; white-space: nowrap; }

.dashboard__avatar { display: flex; align-items: center; justify-content: center; width: 30px; height: 30px; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; overflow: hidden; min-width: 30px; margin-right: 10px; }

.dashboard__text { color: #000; font-size: 12px; letter-spacing: -0.3px; }

.dashboard__text > a { display: block; }

.dashboard__text span { color: #979797; display: inline-block; }

.dashboard__text span::before { content: "|"; margin: 0 4px 0 2px; }

.dashboard__text > small { font-size: 11px; color: #979797; }

.dashboard__body { display: none; margin-top: 13px; padding-left: 40px; font-size: 12px; line-height: 17px; }

.dashboard__body p { padding-bottom: 16px; }

.dashboard__body p:last-of-type { padding-bottom: 0; }

.dashboard__body p > b { font-weight: 400; }

.dashboard__btn { position: relative; bottom: 10px; }

@media only screen and (max-width: 370px) { .dashboard__text span::before { display: none; } }

.settings { width: 100%; }

.settings__list { width: 100%; margin: 12px 0 80px; }

.settings__list > li { border-top: 1px solid #d5d5e0; padding: 20px 26px; }

.settings__list > li:last-of-type { border-bottom: 1px solid #d5d5e0; }

.settings__list > li > span { display: block; color: #979797; margin-bottom: 10px; }

.settings__list > li > a { display: block; color: #000; margin-bottom: 10px; }

.settings__list > li > a:last-of-type { margin-bottom: 0; }

.settings__list > li > a.open-pop-up { color: #7e20c8; }

.settings .version { font-size: 8px; letter-spacing: -0.3px; color: #979797; display: block; margin: -2px 0 25px; }

.settings .logo-bottom { display: flex; justify-content: center; }

.log-out { padding: 21px 14px 20px; }

.log-out p { font-size: 11px; line-height: 24px; color: #979797; letter-spacing: -0.41px; padding: 0; }

.log-out .line { border-top: 1px solid #dadae4; margin: 18px 0; }

.personal-details .user__header { margin: 30px auto 36px; }

.personal-details__list { padding: 0 26px; margin-bottom: 50px; }

.personal-details__list span { display: block; color: #979797; font-size: 11px; letter-spacing: -0.41px; }

.personal-details__list > li { margin-bottom: 18px; position: relative; margin-bottom: 20px; }

.personal-details__list > li .style-input-text { font-size: 15px; height: 20px; }

.personal-details__list > li .style-input-text[disabled] { color: #000; }

.personal-details__list > li .edit-info { position: absolute; top: 23px; right: 0; }

.buy-xim { padding: 48px 24px 0; }

.buy-xim__group-icon { margin-bottom: 18px; }

.buy-xim__group-icon > img { margin: 0 38px; }

.buy-xim .mb-7 { margin-bottom: 7px; }

.buy-xim > p { margin-bottom: 40px; line-height: 21px; letter-spacing: -0.41px; }

.buy-xim__block { width: 246px; height: 142px; margin: 0 auto 41px; position: relative; padding: 10px 32px; color: #fff; font-size: 20px; background-color: #9e58d6; border: 2px solid #7e20c8; border-radius: 25px; -webkit-border-radius: 25px; -moz-border-radius: 25px; -ms-border-radius: 25px; -o-border-radius: 25px; }

.buy-xim .btn-pink { width: 246px; height: 50px; box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); border-radius: 25px; -webkit-border-radius: 25px; -moz-border-radius: 25px; -ms-border-radius: 25px; -o-border-radius: 25px; position: absolute; bottom: -12px; left: -2px; }

.buy-xim .btn-white { margin: 16px 0; width: 100%; }

@media only screen and (max-width: 370px) { .buy-xim { padding: 30px 14px 0; } }

.location-page { padding: 9px 14px; min-height: 100vh; text-align: center; display: flex; align-items: flex-end; justify-content: center; }

.location-page__body { width: 100%; min-height: calc(100vh - 137px); padding: 0 13.5px; background: #ffffff; box-shadow: -2px 2px 10px rgba(0, 0, 0, 0.2); border-radius: 10px; -webkit-border-radius: 10px; -moz-border-radius: 10px; -ms-border-radius: 10px; -o-border-radius: 10px; display: flex; align-items: center; justify-content: space-between; flex-direction: column; }

.location-page__body p { margin: 52px 0 20px; font-size: 20px; line-height: 24px; }

.location-page__body img { margin-bottom: 31px; }

.location-page__body .btn { width: 100%; max-width: 320px; display: flex; margin: 0 auto; }

.location-page__body .btn-border { margin: 10px auto 20px; }

.location-page__header, .location-page__footer { width: 100%; }

.location-page__number { width: 100%; max-width: 320px; height: 40px; background: #f5f5f5; border-radius: 10px; -webkit-border-radius: 10px; -moz-border-radius: 10px; -ms-border-radius: 10px; -o-border-radius: 10px; display: flex; margin: 0 auto 120px; }

.location-page__input { width: 100%; height: inherit; background: transparent; border: none; border-radius: 10px; -webkit-border-radius: 10px; -moz-border-radius: 10px; -ms-border-radius: 10px; -o-border-radius: 10px; box-shadow: none; font-size: 15px; line-height: normal; padding: 0 20px; color: #000; text-align: left; }

.location-page__input::placeholder { color: #979797; letter-spacing: -0.24px; }

/* style select */
.jq-selectbox { min-width: 99px; height: inherit; }

.jq-selectbox__select { display: flex; align-items: center; width: 100%; height: inherit; padding: 0 22px 0 10px; background: #7e20c8; font-family: "Montserrat", sans-serif; font-size: 15px; color: #fff; font-weight: 400; border-radius: 10px; -webkit-border-radius: 10px; -moz-border-radius: 10px; -ms-border-radius: 10px; -o-border-radius: 10px; }

.jq-selectbox__select-text { width: 100% !important; }

.jq-selectbox__trigger { position: absolute; top: 0; right: 0; width: 22px; height: 100%; }

.jq-selectbox__trigger-arrow { position: absolute; top: 50%; left: 0; transform: translateY(-50%); -webkit-transform: translateY(-50%); -moz-transform: translateY(-50%); -ms-transform: translateY(-50%); -o-transform: translateY(-50%); }

.jq-selectbox__trigger-arrow::before { content: url(../img/icons/arrow-down-white.svg); }

.jq-selectbox__dropdown { width: 100%; max-height: 170px; background: #f5f5f5; font-family: "Montserrat", sans-serif; font-size: 15px; color: #000; text-align: left; border-radius: 10px; -webkit-border-radius: 10px; -moz-border-radius: 10px; -ms-border-radius: 10px; -o-border-radius: 10px; padding-top: 20px; margin-top: -10px; z-index: -1; overflow-y: auto; }

.jq-selectbox li { padding: 0 10px; margin-bottom: 20px; }

.jq-selectbox li:last-of-type { margin-bottom: 10px; }

@media only screen and (max-height: 570px) { .location-page { padding: 20px 14px 9px; }
  .location-page__body { min-height: calc(100vh - 30px); } }

.meetup { padding: 36px 25px 0; max-width: 450px; margin: 0 auto; }

.meetup__user { margin: 41px 0 36px; }

.meetup__user .line { display: inline-block; width: 12px; height: 2px; background-color: #7e20c8; border-radius: 1px; -webkit-border-radius: 1px; -moz-border-radius: 1px; -ms-border-radius: 1px; -o-border-radius: 1px; }

.meetup__user .line:first-of-type { margin-right: 5px; }

.meetup__circle { position: relative; width: 100px; height: 100px; border: 2.5px solid #7e20c8; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; }

.meetup__avatar { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); -webkit-transform: translate(-50%, -50%); -moz-transform: translate(-50%, -50%); -ms-transform: translate(-50%, -50%); -o-transform: translate(-50%, -50%); display: flex; align-items: center; justify-content: center; width: 83px; height: 83px; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; overflow: hidden; }

.meetup__avatar > img { width: 100%; height: 100%; object-fit: cover; }

.meetup__list { margin-bottom: 10px; }

.meetup__list > li { position: relative; padding: 12px 40px 12px 0; border-top: 1px solid #d5d5e0; }

.meetup__list > li:last-of-type { border-bottom: 1px solid #d5d5e0; }

.meetup__list > li .invite { display: block; width: 30px; height: 30px; position: absolute; top: 50%; margin-top: -15px; right: 0; }

.meetup .btn-violet { display: flex; margin: 30px auto 10px; }

.meetup.waiting-invite .waiting .line { background-color: #d5d5e0; }

.meetup.waiting-invite .meetup__circle_right { border-color: #d5d5e0; }

.meetup.waiting-invite .invite { display: none; }

.meetup.waiting-invite .btn-meetup { display: none; }

@media only screen and (max-width: 370px) { .meetup { padding: 36px 14px 0; }
  .meetup__user .line { width: 10px; }
  .meetup__circle { width: 88px; height: 88px; border: 2px solid #7e20c8; }
  .meetup__avatar { min-width: 73px; height: 73px; width: 73px; }
  .meetup__list > li { padding: 12px 36px 12px 0; }
  .meetup__list > li .invite { width: 26px; height: 26px; } }

.footer { height: 60px; }

.footer__fixed { position: fixed; bottom: 0; left: 0; right: 0; z-index: 100; background-color: #fff; border-top-left-radius: 10px; border-top-right-radius: 10px; box-shadow: -4px 0px 8px rgba(86, 91, 123, 0.2); }

.footer__body { padding: 18px 10px; display: flex; align-items: center; justify-content: space-around; font-weight: 500; font-size: 12px; }

.footer__body > a { display: flex; }
